<template>
  <div :class="['enter',type]">
    <!-- <div class="verse">
      <span v-for="(item,index) in verse" :key="index" class="verse-item-span">{{item}}</span>
    </div>-->
    <Wave :type="type" />
    <Menu v-if="type === 'menu'" />
    <Flash @click="handleFlash" :type="type" />
    <div :class="['tab-menu',type]"></div>
    <div :class="['load',loadType]" />
  </div>
</template>

<script>
import Wave from "components/wave";
import Flash from "components/flash";
import Cat from "components/cat";
import Menu from "components/menu";
export default {
  name: "Enter",
  components: {
    Wave,
    Flash,
    Cat,
    Menu
  },
  computed: {},
  watch: {},
  data() {
    return {
      verse: ["危机意识", "沟通能力", "自控能力", "成本观念", "自我认知"],
      type: "home",
      loadType: "",
      timeoutkey: null,
      loadTypetimeoutkey: null
    };
  },
  mounted() {},
  methods: {
    handleFlash() {
      this.loadType = "go";

      clearTimeout(this.timeoutkey);
      this.timeoutkey = setTimeout(() => {
        this.loadType = "stop";
        this.type =
          (this.type === "home" && "menu") || (this.type === "menu" && "home");
      }, 800);

      clearTimeout(this.loadTypetimeoutkey);
      this.loadTypetimeoutkey = setTimeout(() => {
        this.loadType = "";
      }, 1400);
    }
  }
};
</script>
<style lang="scss" scoped>
.enter {
  width: 100%;
  height: 100%;
  &.menu {
    background: rgba(243, 190, 29, 1);
  }
  &.home {
    background: url("~img/z7-home.jpg") no-repeat;
    background-size: cover;
  }
  .tab-menu {
    &.home {
      background: linear-gradient(
        45deg,
        rgba(0, 0, 0, 0) 50%,
        rgba(243, 190, 29, 1) 50%
      );
      color: #000;
    }
    &.menu {
      background: linear-gradient(
        45deg,
        rgba(0, 0, 0, 0) 50%,
        rgba(0, 172, 193, 1) 50%
      );
      color: #fff;
    }
    position: absolute;
    width: 30vh;
    height: 30vh;
    top: 0;
    right: 0;
    display: flex;
    .label {
      font-size: 24px;
      transform: translate(45deg);
    }
  }
  .verse {
    position: absolute;
    top: 10vh;
    right: 5vh;
    display: flex;
    flex-direction: row-reverse;
    .verse-item-span {
      height: 100%;
      width: 25px;
      font-size: 16px;
      line-height: 18px;
      margin: 0 5px;
      color: rgb(27, 166, 110);
    }
  }
  .load {
    width: 100%;
    height: 0%;
    display: block;
    position: absolute;
    z-index: 99999;
    background-color: transparent;
    bottom: 0;
    left: 0;
    &.go {
      transition: all 0.8s;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.8);
    }
    &.stop {
      transition: all 1s;
      height: 100%;
      width: 0%;
      background-color: rgba(255, 255, 255, 0);
    }
  }
}
</style>

<style lang="scss">
.enter {
  position: relative;
  overflow: hidden;
  .wave {
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .cat {
    top: 18%;
    right: 0;
  }
}
</style>